// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Mint from 'mint-ui';
import App from './App'
import store from './store/index'
import router from './router'
import mixins from './mixins'

import 'mint-ui/lib/style.css'
import './common/stylus/index.styl'
import './common/resize/resize.js'
import '../static/css/reset.css'


Vue.use(Mint);

// import { Swipe, SwipeItem } from 'mint-ui';

// Vue.component(Swipe.name, Swipe);
// Vue.component(SwipeItem.name, SwipeItem);

Vue.config.productionTip = false
Vue.config.CLIENTMODE = false//是否是手机模式 true:手机模式，false:电脑模式


Vue.mixin(mixins)

router.beforeEach((to, from, next) => {
    if(Vue.config.CLIENTMODE){
        store.dispatch('isShowHeader',false)
        store.dispatch('setTitle',to.meta.title)
    }else{
        if (to.meta.title) {
            store.dispatch('isShowHeader',true)
            store.dispatch('setTitle',to.meta.title)
        }
    }
    next()
  })


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})


