<template>
    <div class="lists">
        <div class="agent">
            <div class="content" v-for="(item,index) in lists0" :key="index">
                <div class="header border-1px">
                    <div class="title">{{item.title}}</div>
                    <div class="reserve">
                        <span class="text">{{item.text}}</span>
                        <span class="text number">{{item.number}}</span>
                    </div>
                </div>
                <ul class="list" v-if="item.type === '询价'">
                    <li>
                        <span class="l-t">{{item.person}}</span>
                        <span class="l-v">{{item.name}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.dateTitle}}</span>
                        <span class="l-v">{{item.date}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.reservationProduct}}</span>
                        <span class="l-v">{{item.product}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.reservationTel}}</span>
                        <span class="l-v">{{item.tel}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.stateText}}</span>
                        <span class="l-v" :class='item.state === 1 ? "finish" : "state"'>{{item.state === 1 ? "已处理" : "未处理"}}</span>
                    </li>
                </ul>
                <ul class="list" v-if="item.type === '面签'">
                    <li>
                        <span class="l-t">{{item.person}}</span>
                        <span class="l-v">{{item.name}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.financialProduct}}</span>
                        <span class="l-v">{{item.product}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.contractAmount}}</span>
                        <span class="l-v">{{item.amount}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.loanDuration}}</span>
                        <span class="l-v">{{item.duration}}</span>
                    </li>
                    <li>
                        <span class="l-t">{{item.stateText}}</span>
                        <span class="l-v" :class='item.state === 1 ? "finish" : "state"'>{{item.state === 1 ? "已处理" : "未处理"}}</span>
                    </li>
                </ul>
                <ag-handle :state="item.state" :type="item.type" :productid="item.productid"></ag-handle>
            </div>
        </div>
        <div class="end">
            <div class="content" v-for="list in lists1" :key="list.id">
                <div class="item">
                    <span class="text">尊敬的客户您好：</span>
                    <span class="text">{{list.desc}}</span>
                </div>
                <Handle :date="list.date" :state="list.state" :productid="list.productid"></Handle>  
            </div>
        </div>
    </div>
