<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl"
.lists
    .agent
        .content
            position :relative
            margin-bottom :0.2rem
            padding :0.24rem 0.31rem
            background-color :#ffffff
            .header
                display :flex
                flex-direction :row
                justify-content :space-between
                align-items :center
                border-1px(#DDDDDD)
                padding-bottom :0.23rem
                .title
                    font-size :0.3rem
                    line-height :0.42rem
                    height :0.42rem
                    color :#232323
                .reserve
                    .text
                        font-size :0.26rem
                        line-height :0.37rem
                        color :#333333
            .list
                margin-top :0.22rem
                li
                    display :flex
                    flex-direction :row
                    padding-bottom :0.13rem
                    font-size :0.26rem
                    line-height :0.37rem
                    height :0.37rem
                    .l-t
                        flex :0 0 1.3rem
                        margin-right :0.43rem
                        color :#999999
                    .l-v
                        flex :1
                        color :#333333 
                        &.finish
                            color :#5C92FB
                        &.state
                            color :#DE0313
                        
    .end
        .content
            margin-bottom :0.2rem
            padding: 0.36rem 0.31rem 0.42rem
            background-color : #FFFFFF
            .item
                display: flex
                flex-direction: column
                margin-bottom: 0.3rem
                .text
                    font-size :0.28rem
                    line-height :0.4rem
                    color :#333333

</style>

