<template>
   <div class="contract">
       <i class="finger"></i>
       <div class="name">
           {{name}}
       </div>
       <div class="login_pwd">
           <div class="login_left">
               登录密码
           </div>
           <div class="login_right">
               <input type="password" placeholder="请输入登录密码" class="psw">
           </div>
       </div>
       <button class="next">
           下一步
       </button>
   </div>
</template>
<script>
import { Toast, Switch } from 'mint-ui';
export default {
  name: 'fingerprint',
  data () {
    return {
       value:true,
       name:"钱多多"
    }
  },
  methods:{
      next(){
        //   this.$router.push({path: '/registerpwd'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/mixin.styl"
    .contract{
        width: 7.5rem;
        padding-top: 0.98rem;
        background: #ffffff;
    }
    .finger{
        display: block;
        width: 1.28rem;
        height: 1.28rem;
        margin: 0 auto 0;
        bg-image('user_head_landing');
        background-size :100% 100%;
        background-repeat :no-repeat;
    }
    .name{
        width: 7.5rem;
        font-family: 'PingFangSC-Semibold';
        font-size: 0.3rem;
        color: #333333;
        line-height: 0.42rem;
        text-align: center;
        margin-top: 0.19rem;
    }
    .login_pwd{
        width: 7.5rem;
        margin-top: 0.98rem;
        overflow: hidden;
    }
    .login_left{
        float: left;
        font-family: 'PingFangSC-Regular';
        font-size: 0.3rem;
        color: #333333;
        line-height: 0.72rem;
        margin-left: 0.3rem;
    }
    .login_right{
        float: left;
        margin-left: 0.382rem;
        width: 5.218rem;
        border-bottom: 1px solid #DFDFDF;
        height: 0.72rem;
    }
    .psw{
        line-height: 0.42rem;
        border: none;
        width: 100%;
        height: 100%;
        outline:none;
        text-indent: 1em;
    }
    .next{
        width: 5.6rem;
        height: 0.8rem;
        background-image: linear-gradient(-133deg, #FF4D4D 0%, #DD0011 100%);
        margin: 1rem 0.95rem 5.66rem 0.95rem;
        border: none;
        outline:none;
        font-family: 'PingFangSC-Regular';
        font-size: 0.3rem;
        color: #FFFFFF;
    }
</style>




