import Vue from 'vue'
import Router from 'vue-router'

import About from '@/views/AboutUs/about'//关于我们
import Branch from '@/views/Branch/branch'//网点分布
import Contact from '@/views/ContactUs/contactUs'//联系我们
import Product from '@/views/ProductIntroductions/product'//产品介绍
import Activities from '@/views/ActivitiesDetails/activitiesDetails' //活动详情
import Prepayment from '@/views/Prepayment/prepayment'//提前还款
import PrepaymentDetail from '@/views/Prepayment/prepaymentDetail'//还款信息填写
import PrepaymentConfirm from '@/views/Prepayment/prepaymentConfirm'//还款信息确认
import Lists from '@/views/Todolist/list'//待办清单-代理商、终端客户

import ContractQry from '@/views/SpecialTeam/ContractQry'//合同查询
import ContractPre from '@/views/SpecialTeam/ContractPre'//合同明细
import ContractDetail from '@/views/SpecialTeam/ContractDetail'//合同详情
import BasicInfo from '@/views/BusinessApply/BasicInfo'//个人业务申请
import ShareholderInfo from '@/views/BusinessApply/ShareholderInfo'//添加股东信息
import GuaranteeInfo from '@/views/BusinessApply/GuaranteeInfo'//担保信息
import EBasicInfo from '@/views/BusinessApply/EBasicInfo'//企业业务申请
import AddGuarantee from '@/views/BusinessApply/AddGuarantee'//添加个人担保
import AddEGuarantee from '@/views/BusinessApply/AddEGuarantee'//添加法人担保
import AssetInfo from '@/views/BusinessApply/AssetInfo'//资产信息
import AddAssetInfo from '@/views/BusinessApply/AddAssetInfo'//添加资产
import FinancingInfo from '@/views/BusinessApply/FinancingInfo'//融资信息
import RepayPlan from '@/views/BusinessApply/RepayPlan'//还款计划预览

import CreditQry from '@/views/CreditApply/CreditQry'//征信查询
import CreditApply from '@/views/CreditApply/CreditApply'// 个人征信申请-代理商
import ECreditApply from '@/views/CreditApply/ECreditApply'// 企业征信申请-代理商
import PCreditApply from '@/views/CreditApply/PCreditApply'// 个人征信申请-终端
import PECreditApply from '@/views/CreditApply/PECreditApply'// 企业征信申请-终端

import Appoint from '@/views/buycar/appoint'//购车预约申请
import Detail from '@/views/buycar/detail' //购车预约详情
import BuyCar from '@/views/buycar/index' //购车预约
import ProgressQuery from '@/views/ProgressQuery/progressQuery' //处理进度查询
import CreditQuery from '@/views/CreditQuery/creditQuery' //征信结果查询
import AppointDeal from '@/views/Appointdeal/appointDeal' //预约处理
import Loan from '@/views/loanTrial/index' //贷款
import LoanTrial from '@/views/loanTrial/loantrial' //贷款预约
import FinanceProducts from '@/views/loanTrial/financeProducts'//金融产品选择
import RepayScan from '@/views/loanTrial/repayScan'//还款预览





//账单汇总-->明细
import bill from '@/views/bill/index'
import billSummary from '@/views/bill/billSummary'
import Detailed from '@/views/bill/Detailed'
import Historys from '@/views/bill/History'
import repayment from '@/views/bill/repayment'
import overdue from '@/views/bill/overdue'

//面签
import face from '@/views/faceSign/index'
import contractTemplate from '@/views/faceSign/contractTemplate'
import contractInformation from '@/views/faceSign/contractInformation'
import settlement from '@/views/faceSign/settlement'
import faceId from '@/views/faceSign/faceId'
import peopleFace from '@/views/faceSign/peopleFace'
import autograph from '@/views/faceSign/autograph'

//我的
import mine from '@/views/mine/index'
import fingerprint from '@/views/mine/fingerprint'
import gesture from '@/views/mine/gesture'
import loginChange from '@/views/mine/loginChange'
import emailChange from '@/views/mine/emailChange'
import phoneChange from '@/views/mine/phoneChange'

Vue.use(Router)

let router= new Router({
    // mode:'history',//去#号
    routes: [
    {
        path: '/ContractQry',
        name: 'ContractQry',
        component: ContractQry,
        meta:{
            title:"合同查询"
        }
    },
    {
        path: '/about',
        name: 'About',
        component: About,
        meta:{
            title:"关于我们"
        }
    },
    {
        path: '/branch',
        name: 'Branch',
        component: Branch,
        meta:{
            title:"网点分布"
        }
    },
    {
        path: '/contactUs',
        name: 'Contact',
        component: Contact,
        meta:{
            title:"联系我们"
        }
    },
    {
        path: '/productIntroductions',
        name: 'Product',
        component: Product,
        meta:{
            title:"产品介绍"
        }
    },
    {
        path: '/activities',
        name: 'Activities',
        component: Activities,
        meta:{
            title:"活动详情"
        }
    },
    {
        path: '/prepayment',
        name: 'Prepayment',
        component: Prepayment,
        meta:{
            title:"提前还款"
        }
    },
    {
        path: '/prepaymentDetail',
        name: 'PrepaymentDetail',
        component: PrepaymentDetail,
        meta:{
            title:"还款信息填写"
        }
    },
    {
        path: '/prepaymentConfirm',
        name: 'PrepaymentConfirm',
        component: PrepaymentConfirm,
        meta:{
            title:"提前还款确认"
        }
    },
    {
        path: '/lists',
        name: 'Lists',
        component: Lists,
        meta:{
            title:"待办清单"
        }
    },
    {
        path: '/ContractDetail',
        name: 'ContractDetail',
        component: ContractDetail,
        meta:{
            title:"合同详情"
        }
    },
    {
        path: '/ContractPre',
        name: 'ContractPre',
        component: ContractPre,
        meta:{
            title:"合同明细"
        }
    },
    {
        path: '/BasicInfo',
        name: 'BasicInfo',
        component: BasicInfo,
        meta:{
            title:"业务申请填写"
        }
    },
    {
        path: '/ShareholderInfo',
        name: 'ShareholderInfo',
        component: ShareholderInfo,
        meta:{
            title:"添加股东信息"
        }
    },
    {
        path: '/GuaranteeInfo',
        name: 'GuaranteeInfo',
        component: GuaranteeInfo,
        meta:{
            title:"业务申请填写"
        }
    },
    {
        path: '/EBasicInfo',
        name: 'EBasicInfo',
        component: EBasicInfo,
        meta:{
            title:"业务申请填写"
        }
    },
    {
        path: '/AddGuarantee',
        name: 'AddGuarantee',
        component: AddGuarantee,
        meta:{
            title:"添加个人担保"
        }
    },
    {
        path: '/AddEGuarantee',
        name: 'AddEGuarantee',
        component: AddEGuarantee,
        meta:{
            title:"添加法人担保"
        }
    },
    {
        path: '/AssetInfo',
        name: 'AssetInfo',
        component: AssetInfo,
        meta:{
            title:"业务申请填写"
        }
    },
    {
        path: '/AddAssetInfo',
        name: 'AddAssetInfo',
        component: AddAssetInfo,
        meta:{
            title:"添加资产"
        }
    },
    {
        path: '/FinancingInfo',
        name: 'FinancingInfo',
        component: FinancingInfo,
        meta:{
            title:"业务申请填写"
        }
    },
    {
        path: '/RepayPlan',
        name: 'RepayPlan',
        component: RepayPlan,
        meta:{
            title:"还款计划预览"
        }
    },
    {
        path: '/CreditQry',
        name: 'CreditQry',
        component: CreditQry,
        meta:{
            title:"征信查询"
        }
    },
    {
        path: '/CreditApply',
        name: 'CreditApply',
        component: CreditApply,
        meta:{
            title:"征信申请"
        }
    },
    {
        path: '/ECreditApply',
        name: 'ECreditApply',
        component: ECreditApply,
        meta:{
            title:"征信申请"
        }
    },
    {
        path: '/PCreditApply',
        name: 'PCreditApply',
        component: PCreditApply,
        meta:{
            title:"征信申请"
        }
    },
    {
        path: '/PECreditApply',
        name: 'PECreditApply',
        component: PECreditApply,
        meta:{
            title:"征信申请"
        }
    },
    //购车预约
    {
        path:'/buycar',
        meta: {
            requireAuth: true,
          },
        name: "buycar",
        component: BuyCar,
        redirect: '/buycar/detail',
        children: [
            {
                path: '/buycar/detail',
                name: "detail",
                component: Detail,
                meta: {
                    title: "预约详情"
                }

            },
            {
                path: '/buycar/appoint',
                name: "appoint",
                component: Appoint,
                meta: {
                    title: "预约申请"
                }
            }
        ]
    },
      {
        path: '/progressquery',
        name: 'progressquery',
        meta: {
          requireAuth: true,
          title:'进度查询'
       },
        component: ProgressQuery
      },
      {
        path: '/appointdeal',
        name: 'appointdeal',
        meta: {
          requireAuth: true,
          title:'预约处理'
       },
        component: AppointDeal
      },
      {
        path: '/creditquery',
        name: 'creditquery',
        meta: {
          requireAuth: true,
          title:'征信查询'
       },
        component: CreditQuery
      },
      //贷款试算
      {
        path:'/loan',
        meta: {
            requireAuth: true,
          },
        name: "loan",
        component: Loan,
        redirect: '/loan/loantrial',
        children: [
               {
                path: '/loan/loantrial',
                name: "loantrial",
                component: LoanTrial,
                meta: {
                    title: "贷款试算"
                }
               },
               {
                path: '/loan/financeproducts',
                name: "financeproducts",
                component: FinanceProducts,
                meta: {
                    title: "金融产品选择"
                }
               },
               {
                path: '/loan/repayscan',
                name: "repayscan",
                component: RepayScan,
                meta: {
                    title: "还款计划预览"
                }
            }
        ]
    },
     //账单
      {
        path:'/bill',
        meta: {
            requireAuth: true,
          },
        name: "bill",
        component: bill,
        redirect: '/bill/billSummary',
        children: [
            {
                path: '/bill/billSummary',
                name: "billSummary",
                component: billSummary,
                meta:{
                    title:"账单汇总"
                }
            },
            {
                path: '/bill/Detailed',
                name: "Detailed",
                component: Detailed,
                meta:{
                    title:"账单明细"
                }
            },
            {
                path:"/bill/History",
                name:"Historys",
                component: Historys,
                meta:{
                    title:"历史账单明细"
                }
            },
            {
                path:"/bill/repayment",
                name:"repayment",
                component: repayment,
                meta:{
                    title:"账单明细"
                }
            },
            {
                path:"/bill/overdue",
                name:"overdue",
                component: overdue,
                meta:{
                    title:"逾期合同"
                }
            }
        ]
    },
    //面签
    {
        path:'/face',
        meta: {
            requireAuth: true,
          },
        name: "face",
        component: face,
        redirect: '/face/contractTemplate',
        children: [
            {
                path: '/face/contractTemplate',
                name: "contractTemplate",
                component: contractTemplate,
                meta: {
                    title: "面签"
                }

            },
            {
                path: '/face/contractInformation',
                name: "contractInformation",
                component: contractInformation,
                meta: {
                    title: "合同信息"
                }
            },
            {
                path: "/face/settlement",
                name: "settlement",
                component: settlement,
                meta: {
                    title: "合同结清"
                }
            },
            {
                path: "/face/faceId",
                name: "faceId",
                component: faceId,
                meta: {
                    title: "面签操作"
                }
            },
            {
                path: "/face/peopleFace",
                name: "peopleFace",
                component: peopleFace,
                meta: {
                    title: "面签操作"
                }
            },
            {
                path: "/face/autograph",
                name: "autograph",
                component: autograph,
                meta: {
                    title: "面签操作"
                }
            }
        ]
    },
    //我的
    {
        path:'/mine',
        meta: {
            requireAuth: true,
          },
        name: "mine",
        component: mine,
        redirect: '/mine/fingerprint',
        children: [
            {
                path: '/mine/fingerprint',
                name: 'fingerprint',
                component: fingerprint,
                meta: {
                    title: '指纹密码设置'
                }

            },
            {
                path: '/mine/gesture',
                name: 'gesture',
                component: gesture,
                meta: {
                    title :'手势密码设置'
                }
            },
            {
                path: '/mine/loginChange',
                name: 'loginChange',
                component: loginChange,
                meta: {
                    title: '手机号修改'
                }
            },
            {
                path: '/mine/emailChange',
                name: 'emailChange',
                component: emailChange,
                meta: {
                    title: '邮箱修改'
                }
            },
            {
                path: '/mine/phoneChange',
                name: 'phoneChange',
                component: phoneChange,
                meta: {
                    title: '手机号修改'
                }
            },
        ]
    },

      {
        path: '/',
        redirect: '/ContractQry'
      },

]
})

export default router


