<template>
    <div class="repayscan">
          <div class="hetong">
              <p class="title">合同号 SANY20187633748</p>
              <div class="hetong_list">
                   <div>
                      <span class="ht">合同金额</span>
                      <span class="wt">1,300,000</span>
                  </div>
                  <div>
                      <span class="ht">融资金额</span>
                      <span class="wt">1,300,000</span>
                  </div>
                  <div>
                      <span class="ht">合同利率</span>
                      <span class="wt">3%</span>
                  </div>
                  <div>
                      <span class="ht">合同期限</span>
                      <span class="wt">2016/12/21-2019/12/20</span>
                  </div>
                  <div>
                      <span class="ht">还款方式</span>
                      <span class="wt">等额本息</span>
                  </div>
              </div>
          </div>
          <div class="repayDetail">
              <p class="title">还款计划明细</p>
               <div class="scan-t">
                   <div>还款日期</div>
                   <div>还款金额</div>
                   <div>贷款余额</div>
               </div>
               <div class="detail-list">
                   <ul ref="showOrHide">
                       <li v-for="(item,index) in items" :key="index">
                           <span>{{item.repaydate}}</span>
                           <span class="repayamount">{{item.repayamount}}</span>
                           <span class="loanrest">{{item.loanrest}}</span>
                       </li>
                   </ul>
                   <div class="more" v-if="items.length > 3">
                       <button @click="more($event)">更多展开</button>
                   </div>
               </div>
          </div>
          <div class="total">
                总计 1,300,000
          </div>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'repayscan',
  data () {
    return {
        flag:true,
       items:[
                {
                    repaydate:'2016/12/21 (1期)',
                    repayamount:'1,300,000',
                    loanrest:'10,000'
                },
                {
                    repaydate:'2016/12/21 (1期)',
                    repayamount:'1,300,000',
                    loanrest:'10,000'
                },
                 {
                    repaydate:'2016/12/21 (1期)',
                    repayamount:'1,300,000',
                    loanrest:'10,000'
                },
                 {
                    repaydate:'2016/12/21 (1期)',
                    repayamount:'1,300,000',
                    loanrest:'10,000'
                }
             ]
       }
  },
  methods:{
      more(e){
          if(this.flag){
            this.$refs.showOrHide.style.height = 'auto';
            e.target.innerText = '收起更多'
            this.flag = false;
          }else{
            this.$refs.showOrHide.style.height = 120 + 'px';
            e.target.innerText = '更多展开'
            this.flag = true;
          }
      }
  }
}
</script>
<style lang="scss" scoped>
   .repayscan{
       .hetong{
           width: 90%;
           background: #fff;
           margin: 3% auto;
           .title{
               color: #232323;
               font-size: 0.3rem; 
               padding: 3%;
           }
           .hetong_list{
               div{
                    margin: 3%;
                    padding-bottom: 5px;
                    .ht{
                       color: #999999;
                       font-size: 0.26rem;
                       padding: 3%;
                    }
                  .wt{
                      color: #333333;
                      font-size: 0.26rem;
                      padding: 3%;
                 }
               }
           }
       }
       .repayDetail{
           width: 90%;
           background: #fff;
           margin: 3% auto;
           .title{
               color: #232323;
               font-size: 0.3rem; 
               padding: 3%;
           }
           .scan-t{
              display: flex;
              color: #999999;
              margin-left: 6%;
              div{
                 flex-grow: 1;
                 text-align: center;
              }
           }
           .detail-list{
                ul{
                    height: 120px;
                    overflow-y: hidden;
                    li{
                        height: 25px;
                        line-height: 25px;
                        margin: 3%;
                   .repayamount{
                        margin-left: 5%;
                   }
                   .loanrest{
                       margin-left: 10%;
                  }
               }  
             }
             .more{
                 text-align: center;
                 padding-bottom: 10px;
                 button{
                     padding: 5px;
                     border: 1px solid #6E9EFC;
                     color: #6E9EFC;
                     background: none;
                     outline: none;
                 }
             }
         }
               
      }
      .total{
          text-align: center;
          margin: 3%;
      }
   }
</style>




