<template>
  <div>
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,index) in items" :key="index">
           <img :src="item.pic_src" class="img"/>
           <p class="decsri">{{item.title}}</p>
      </mt-swipe-item>
    </mt-swipe>
      <div class="pic-scan">
         <div class="title"><span class="lines"></span><span>详情</span><span class="lines"></span></div>
         <div v-for="(pic,index) in pics" :key="index">
            <img :src="pic.addr" alt="">
         </div>
      </div>
     <div class="arguments">
       <div class="title"> <span class="lines"></span><span>参数</span><span class="lines"></span></div>
        <table class="table">
             <tr>
               <td>整车参数</td>
               <td>
                  <p>全长：1100mm</p>
                  <p>总高：1100mm</p>
                  <p>总宽：1100mm</p>
                  <p>自重：1100mm</p>
               </td>
             </tr>
             <tr>
               <td>骨架支腿参数</td>
               <td>
                  <p>臂架垂直高度：1100mm</p>
                  <p>臂架水平长度：1100mm</p>
                  <p>臂架垂直深度：1100mm</p>
                  <p>臂架展开高度：1100mm</p>
               </td>
             </tr>
           </table>
     </div>
     <div class="products_bright">
        <div class="title"><span class="lines"></span><span>产品亮点</span><span class="lines"></span></div>
        <p class="products_info">GPS远程监控系统，震动手柄；</p>
        <p class="products_info">发动机排放符合欧美非公路第3阶段标准；使用快换型拉绳接头和卷扬钢丝绳绳头；</p>
        <p class="products_info">采用全电控系统，具有故障自诊断功能，配备电气备用系统;司机室宽敞明亮</p>
     </div>
     <div class="Distributor_command">
         <div class="title"><span class="lines"></span><span>经销商推荐</span><span class="lines"></span> </div>
         <div class="Distributor_msg">
          <div class="company_img">
             <img :src="imgUrl" alt="">
          </div>
          <div class="addre_info">
              <p>{{company_name}}</p>
              <p>电话：{{company_tel}}</p>
              <p>地址：{{company_addr}}</p>
          </div>
        </div>
     </div>
   <mt-button type="danger" size="large" class="now_appoint" @click="now_appoint">立即预约</mt-button>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
    data () {
      return {
        items:[{ pic_src:require('../../imgs/buycar/lunbo.png'),
                 title:'C8系列30米混凝土泵车SYM5180THBES 30C-8'
               },
               { pic_src:require('../../imgs/buycar/lunbo.png'),
                 title:'C8系列30米混凝土泵车SYM5180THBES 30C-8'
               }
          ],
          pics:[{addr:require('../../imgs/buycar/gouche01.jpg')},
               {addr:require('../../imgs/buycar/gouche02.jpg')},
               {addr:require('../../imgs/buycar/gouche03.jpg')},
               {addr:require('../../imgs/buycar/gouche04.jpg')},
               {addr:require('../../imgs/buycar/gouche05.jpg')},
               {addr:require('../../imgs/buycar/gouche06.jpg')},
               {addr:require('../../imgs/buycar/gouche07.jpg')},
               {addr:require('../../imgs/buycar/gouche08.jpg')}
          ],
          imgUrl : require('../../imgs/buycar/gouche08.jpg'),
          company_tel:'0731-1234567',
          company_addr:'湖南省长沙市三一路123号',
          company_name:'湖南省长沙市三一重工代理商'
        }
    },
    methods:{
      now_appoint(){
          this.$router.push({path:'/buycar/appoint'});
      }
    }
  }
</script>

<style scoped>
  img {
    width: 100%;
  }
  .mint-swipe {
    height: 218px;
  }
  .decsri{
    color: #fff;
    position: absolute;
    bottom: 0;
    height: 0.7rem;
    line-height: 0.7rem;
    width: 100%;
    font-size: 0.32rem;
    padding: 0.1rem;
  }
 .title{
    text-align: center;
    margin: 0.5rem auto;
 }
 .title span{
   display: inline-block;
   margin: 0 10px;
   font-size: 0.3rem;
 }
 .lines{
   width: 80px;
   border-bottom: 1px solid #cccccc;
 }
 .products_info{
    width: 90%;
    margin: 10px auto;
    line-height: 0.5rem;
    color: #333333;
    font-size: 0.3rem;
 }
 .Distributor_msg{
   width: 90%;
   margin: auto;
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 0.3rem;
 }
 .company_img img{
     width: 1.6rem;
     height: 1.6rem;
 }
 .addre_info p{
     margin: 0.1rem;
     line-height: 0.5rem;
     font-size: 0.3rem;
 }  
 .now_appoint{
      background: linear-gradient(#FF4D4D, #DD0011);
      width: 80%;
      margin: 5% auto;
   }
   .table{
     width: 90%;
     margin: auto;
     font-size: 0.3rem;
   } 
    .table tr td{
       text-align:center;
       vertical-align: middle;
       padding: 3%;
       border: 1px solid #979797;
    }
    .table tr td p{
       margin: 15px auto;
    }
</style>


