<template>
    <div class="appointdeal">
        <div class="BusinessApplication">
             <div class="title">预约编号 SANY20187633748</div>
             <div class="business">
                   <div>预约姓名</div>
                   <div>钱多多</div>
              </div>
              <div class="business">
                   <div>预约产品</div>
                   <div>挖机</div>
              </div>
              <div class="business">
                   <div>预约手机号</div>
                   <div>13237484839</div>
              </div>
              <div class="business">
                   <div>地区</div>
                   <div>湖南省长沙市长沙县三一路</div>
              </div>
              <div class="business">
                   <div>公司名称</div>
                   <div></div>
              </div>
              <div class="business">
                   <div>购买周期</div>
                   <div>半年内</div>
              </div>
              <div class="business">
                   <div>其他要求</div>
                   <div></div>
              </div>
          </div>

          <div class="additional">
              <mt-field label="回复日期"  type="date" v-model="date"></mt-field>
          </div>
          <div class="additional">
              <mt-field label="回复说明" placeholder="请输入。。" v-model="comment"></mt-field>
          </div>
              <mt-button type="danger" size="large" class="commmit_appoint" @click="commmit_appoint">提交申请</mt-button>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'appointDeal',
  data () {
    return {
        date:'',
        comment:''
    }
  },
  methods:{
      commmit_appoint(){
           Toast({
            message: `已提交，请等待审核。。。`,
            position: 'middle',
            duration: 1000
        });
      }
  }
}
</script>
<style lang="scss" scoped>
  .appointdeal{
      font-size: 0.3rem;
      .BusinessApplication{
          margin: 5%;
          background: #fff;
         .title{
             margin: 3% 0 0 3%;
             color: #232323;
             padding-top: 10px;
          }
          .business{
               display: flex;
               margin: 3%;
               padding-bottom: 5px;
               font-size: 0.26rem;
                div:nth-child(1){
                   color: #999999;
                   width: 30%;
               }
                div:nth-child(2){
                   width: 70%;
               }
          }
      }
      .additional{
          margin: 5%;
      }
      .commmit_appoint{
          width: 90%;
         margin: auto;
      }
  }
</style>




