<template>
   <div class="contract">
       <!-- 为了好看我加的 -->
        <mt-header title="合同信息">
            <router-link to="/" slot="left">
              <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="content_information">
            <div class="middle">
                <div class="title_list">
                    <div class="contract_words">合同号  SNAY201876733748</div>
                    <div class="details">详情</div> 
                </div>
                <ul class="regulations">
                    <li class="regulations_list" v-for="(item,index) in faceSignData" :key="index">
                        <div class="regulations_word">
                            {{item.type}}
                        </div>
                        <div class="regulations_content">
                            {{item.data}}
                        </div>
                    </li>
                </ul>
            </div>
        </div> 
        <button class="start" @click="clickJump">
            开始认证
        </button>
      </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name: 'contractInformation',
  data () {
    return {
        faceSignData:[
                {type:"业务日期",data:"2018/03/31"},
                {type:"借款人姓名",data:"钱多多"},
                {type:"证件号码",data:"371524199309300211"},
                {type:"还款账号",data:"6222222283849284"},
                {type:"金融产品",data:"金宏融资租赁36期"},
                {type:"合同金额",data:"9,000,000"},
                {type:"首付款",data:"2,000,000"},
                {type:"还款频率",data:"一个月"},
                {type:"融资期限",data:"36月"},
                {type:"融资金额",data:"5,000,000"},
                {type:"还款方式",data:"等额本息"},
                {type:"融资比例",data:"70%"},
            ]
    }
  },
  methods:{
       clickJump(){
          this.$router.push({path: '/face/faceId'})
      },
      next(){
        //   this.$router.push({path: '/registerpwd'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style scoped>
    .contract{
        font-family: "PingFangSC-Regular";
    }
    .mint-header{
       background: linear-gradient(#FF4D4D, #DD0011);
       min-height: 0.7rem;   
    }
    .content_information{
        width: 7.5rem;
        height: 10.88rem;
        background: #f4f4f4;
        padding-top: 0.3rem;
    }
    .middle{
        width: 6.9rem;
        height: 7.45rem;
        background: #ffffff;
        margin: 0 auto;
    }
    .title_list{
        display: flex;
        font-size: 0.3rem;
        color: #232323;
        margin-left: 0.31rem;
        padding-top: 0.37rem;
    }
    .contract_words{
        width: 5.68rem;
        line-height: 0.42rem;
    }
    .details{
        color: #DE0313;
        font-size: 0.26rem;
        line-height: 0.42rem;
    }
    .regulations{
        margin-top: 0.34rem;
    }
    .regulations_list{
        margin-left: 0.32rem;
        display: flex;
        margin-top: 0.12rem;
    }
    .regulations_word{
        font-size: 0.26rem;
        line-height: 0.4rem;
        color: #999999;
        width: 2.31rem;
    }
    .regulations_content{
        font-size: 0.28rem;
        color: #333333;
        line-height: 0.4rem;
    }
     .start{
        width: 5.6rem;
        height: 0.8rem;
        margin: 0.2rem 0.95rem;
        background: linear-gradient(#FF4D4D, #DD0011);
        outline:none;
        border: none;
        font-size: 0.3rem;
        color: #FFFFFF;
    }
</style>




