const headerr = {
  state: {
    showHeader: true, //true:显示标题栏，false:隐藏标题栏
    title: "三一重工" //标题名称
  },
  mutations: {
    ISSHOWHEADER: (state, data) => {
      state.showHeader = data
    },
    SETTITLE: (state, data) => {
      state.title = data
    },
  },
  actions: {
    isShowHeader({
      commit
    }, data) {
      commit('ISSHOWHEADER', data)
    },
    setTitle({
      commit
    }, data) {
      commit('SETTITLE', data)
    }
  }
}

export default headerr



