<template>
    <div class="creditQuery">
        <div class="BusinessApplication" v-for="(item,index) in items" :key="index">
             <div class="title">征信查询</div>
             <div class="business">
                   <div>申请人</div>
                   <div>{{item.appoint_name}}</div>
              </div>
              <div class="business">
                   <div>申请日期</div>
                   <div>{{item.appoint_date}}</div>
              </div>
              <div class="business">
                   <div>状态</div>
                   <div class="status">{{item.status}}</div>
              </div>
              <div class="detail">
                  <span @click="detail">查看详情</span>
              </div>
              <div class="divide"></div>
          </div>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'creditQuery',
  data () {
    return {
       items:[{
               appoint_name:'钱多多',
               appoint_date:'2018/12/21',
               status:'通过'
             },
             {
               appoint_name:'钱多多',
               appoint_date:'2018/12/21',
               status:'通过'
             }
       ]  
    }
  },
  methods:{
      detail(){
          Toast({
                message: '呵呵呵。。。。',
                position: 'middle',
                duration: 1000
            });
      }
  }
}
</script>
<style lang="scss" scoped>
  .creditQuery{
      font-size: 0.3rem;
       .title{
             border-bottom: 1px solid #DDDDDD;
             padding-bottom: 10px;
             margin: 3% 0 0 4%;
             color: #232323;
          }
      .divide{
           height: 0.25rem;
           background: #cccccc;
        }
      .BusinessApplication{
         .business{
               display: flex;
               margin: 3%;
               div:nth-child(2){
                  width: 70%;
               }
               div:nth-child(1){
                   color: #999999;
                   width: 30%;
               }
               .status{
                   color: #5C92FB;
               }
          }
          .detail span{
             margin-left: 70%;
             margin-bottom: 3%;
             width: 1.5rem;
             border-radius: 3px;
             height: 0.5rem;
             text-align: center;
             line-height: 0.5rem;
             border: 1px solid #DE0313;
             color: #DE0313;
             padding: 0.08rem;
             display: inline-block;
             cursor: pointer;
             font-size: 0.26rem;
          }
        
      }     
}
</style>




