<template>
   <div class="contract">
       <div v-for="(item,index) in changeData" :key="index" class="login_pwd">
           <div class="login_left">
               {{item.text}}
           </div>
           <div class="login_right">
               <input type="password" :placeholder="item.p" class="psw">
           </div>
       </div>
        <button class="next">
           确定
        </button>
   </div>
</template>
<script>
import { Toast, Switch } from 'mint-ui';
export default {
  name: 'loginChange',
  data () {
    return {
       value:true,
       changeData:[
         {text:"旧密码",p:"请输入旧密码"},
         {text:"新密码",p:"请输入新密码"},
         {text:"确认密码",p:"请输入新密码"}
       ]
    }
  },
  methods:{
      next(){
        //   this.$router.push({path: '/registerpwd'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style scoped>
    .contract{
        width: 7.5rem;
        background: #ffffff;
        padding-top: 0.23rem;
    }
     .login_pwd{
        width: 7.5rem;
        overflow: hidden;
        margin-top: 0.24rem;
    }
    .login_left{
        float: left;
        font-family: 'PingFangSC-Regular';
        font-size: 0.3rem;
        color: #333333;
        line-height: 0.72rem;
        margin-left: 0.3rem;
        width: 1.582rem;
    }
    .login_right{
        float: left;
        width: 5.218rem;
        border-bottom: 1px solid #DFDFDF;
        height: 0.72rem;
    }
    .psw{
        line-height: 0.42rem;
        border: none;
        width: 100%;
        height: 100%;
        outline:none;
        text-indent: 1em;
    }
    .next{
        width: 5.6rem;
        height: 0.8rem;
        background-image: linear-gradient(-133deg, #FF4D4D 0%, #DD0011 100%);
        margin: 1.02rem 0.95rem 0 0.95rem;
        border: none;
        outline:none;
        font-family: 'PingFangSC-Regular';
        font-size: 0.3rem;
        color: #FFFFFF;
    }
   
</style>




