<template>
    <div class="wrapper">
        <div class="header_title">
            <div class="line border-1px"></div>
            <h2 class="title">终端客户金融产品</h2>
            <div class="line border-1px"></div>
        </div>
        <div class="content">
            <div class="sub_title">
                <i class="icon"></i>
                <i class="name">灵活信贷</i>
            </div>
            <div class="lists">
                <p class="text">一、产品介绍：</p>
                <p class="text indent">灵活信贷是为满足客户因南北季节性差异、春节假期、
                    工程施工淡季等因素影响而产生的差异化需求，通过灵活选择还款方式、
                    合理搭配首付和保证金比例、延长还款时间，为客户量身定做的金融产品。</p>
                <p class="text">二、还款方式：</p>
                <p class="text">客户可根据自身差异化需求，选择其中一种：</p>         
                <p class="text">A.等额本息还款</p>     
                <p class="text">B.等额本金还款</p>        
                <p class="text">C.灵活还款</p>     
            </div>
        </div>
        <div class="header_title">
            <div class="line border-1px"></div>
            <h2 class="title">营运资金贷款</h2>
            <div class="line border-1px"></div>
        </div>
        <div class="content">
            <div class="sub_title">
                <i class="icon"></i>
                <i class="name">营运资金贷款</i>
            </div>
            <div class="lists">
                <p class="text">一、产品介绍：</p>
                <p class="text indent">营运资金贷款是指代理商在授信额度内，根据营运需要提取贷款和还款的金融产品。</p>
                <p class="text">二、还款方式：</p>
                <p class="text">A.本金</p>         
                <p class="text">代理商根据自身情况可分批次或一次性偿还贷款本金（需提前3个工作日告知）；</p>     
                <p class="text">B.利息</p>        
                <p class="text">每月15日计收上一计息周期利息，未满一个计息周期按实际天数计算，贷款到期日计收最后一期利息。
                    采取固定利率，以贷款生效日利率为准。</p>     
            </div>
        </div>
    </div>
</template>

<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/mixin.styl"
.wrapper
    position :absolute
    width :100%
    height :100%
    background-color :#ffffff
    .header_title
        display :flex
        justify-content :center
        align-items :center
        margin-top :0.27rem
        .line
            flex :0 0 0.61rem
            border-1px(#979797)
        .title
            margin :0 0.11rem 0 0.11rem
            font-size: 0.32rem
            line-height: 0.45rem
            font-weight :700
            height: 0.45rem
            color: #333333
            font-family: 'PingFangSC-Semibold'
    .content
        margin:0.22rem 0.3rem 0 0.3rem
        .sub_title
            display :flex
            justify-content :flex-start
            align-items: center
            i 
                font-style :normal
            .icon
                flex :0 0 0.1rem
                height :0.3rem
                background-color : #DF0313
            .name
                flex :1
                margin-left :0.1rem
                font-size :0.3rem
                font-family :'PingFangSC-Semibold'
                line-height :0.3rem
                color: #DF0313
        .lists
            margin:0.21rem 0.29rem 0.42rem 0.29rem
            .text
                font-size :0.28rem
                line-height :0.42rem
                font-family :'PingFangSC-Regular'
                color :#333333
                text-align: justify
            .indent
                text-indent:2em      

</style>


<script>
// import { MessageBox ,Popup} from "mint-ui";

// import { Swipe, SwipeItem } from "mint-ui";

export default {
  name: "product",
  data() {
    return {}
  },
  mounted(){

  },
  methods: {
    goBack:function() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
};
</script>




