<template>
  <div class="container ContractQry">
    <mt-search v-model="searchTxt" cancel-text="取消" placeholder="搜索">
    </mt-search>

    <div class="detail-l" v-for="(item,index) in searchList" :key="index">
      <div class="l-title">合同号 SANY20180603432423</div>
      <ul>
        <li>
          <span class="l-t">借款人</span>
          <span class="l-v">钱多多</span>
        </li>
        <li>
          <span class="l-t">合同金额</span>
          <span class="l-v">2000.00</span>
        </li>
        <li>
          <span class="l-t">贷款期限</span>
          <span class="l-v">24月</span>
        </li>
        <li>
          <span class="l-t">融资金额</span>
          <span class="l-v">100000.00</span>
        </li>
        <li>
          <span class="l-t">合同状态</span>
          <span class="l-v blue">正常</span>
        </li>
      </ul>
      <button type="button" @click="showPre()">合同明细</button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchTxt: "",
      List: [
        {
          a: "张三",
          b: "20000",
          c: "24",
          d: "1000",
          e: "正常"
        },
        {
          a: "李四",
          b: "30000",
          c: "12",
          d: "2000",
          e: "正常"
        }
      ]
    };
  },
  computed: {
    //从List中筛选出含有搜索框内容的数据
    searchList() {
      return this.List.filter(data => {
        for (let i in data) {
          if (data[i].indexOf(this.searchTxt) != -1) {
            return true;
          }
        }
        return false;
      });
    }
  },
  mounted(){
    debugger;
  },
  methods: {
    showPre() {
      this.$router.push("/ContractPre");
    }
  }
};
</script>

<style lang="scss" scoped>
$rem: 100;
.blue {
  color: #5c92fb !important;
}
.container.ContractQry {
  margin: 0;
  padding: 0;
  .detail-l {
    background: #ffffff;
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
      // height: 91rem/$rem ;
      // line-height: 91rem/$rem ;
      font-size: 30rem/$rem;
      color: #232323;
      padding: 0 0 0 30rem/$rem;
      padding: 24rem/$rem 0 24rem/$rem 31rem/$rem;
      border-bottom: 1px solid #dddddd;
    }
    button {
      position: absolute;
      right: 30rem/$rem;
      bottom: 30rem/$rem;
      padding: 11rem/$rem 22rem/$rem;
      border: 1px solid #de0313;
      font-size: 26rem/$rem;
      color: #de0313;
      background: #ffffff;
    }
  }
  .detail-l ul {
    margin: 0;
    padding: 20rem/$rem 30rem/$rem;
    list-style: none;
    font-size: 26rem/$rem;

    li {
      display: flex;
      padding: 6rem/$rem 0;
      .l-t {
        flex: 1;
        font-size: 26rem/$rem;
        color: #999999;
        text-align: left;
      }
      .l-v {
        flex: 2;
        font-size: 26rem/$rem;
        color: #333333;
        text-align: left;
      }
    }
  }
}
</style>



