<template>
    <div>
        <v-navbar :selected='selected' :params='params'></v-navbar>
        <div class="container ">
            <div class="detail-l">
                <div class="l-title">
                    <i>个人担保</i>
                    <span class="red" @click="addGuarantee">添加担保人</span>
                </div>
                <div class='bg-white'>
                <ul class='arrow borderB' @click="addGuarantee">
                    <li class='row'>
                        <span class="col-4">姓名</span>
                        <span class="col-6 text-l">钱多多</span>
                    </li>
                    <li class='row'>
                        <span class="col-4">身份证</span>
                        <span class="col-6 text-l">130298198706173545</span>
                    </li>
                </ul>
                <ul class='arrow' @click="addGuarantee">
                    <li class='row'>
                        <span class="col-4">姓名</span>
                        <span class="col-6 text-l">钱多多</span>
                    </li>
                    <li class='row'>
                        <span class="col-4">身份证</span>
                        <span class="col-6 text-l">130298198706173545</span>
                    </li>
                </ul>
                </div>
            </div>

            <div class="detail-l">
                <div class="l-title">
                    <i>法人担保</i>
                    <span class="red" @click="addEGuarantee">添加法人</span>
                </div>
                <div class='bg-white'>
                <ul class='arrow borderB' @click="addEGuarantee">
                    <li class='row'>
                        <span class="col-4">公司名称</span>
                        <span class="col-6 text-l">钱多多</span>
                    </li>
                    <li class='row'>
                        <span class="col-4">统一社会信用代码</span>
                        <span class="col-6 text-l">130298198706173545</span>
                    </li>
                </ul>
                <ul class='arrow' @click="addEGuarantee">
                    <li class='row'>
                        <span class="col-4">公司名称</span>
                        <span class="col-6 text-l">钱多多</span>
                    </li>
                    <li class='row'>
                        <span class="col-4">统一社会信用代码</span>
                        <span class="col-6 text-l">130298198706173545</span>
                    </li>
                </ul>
                </div>
            </div>

            <div class="gap"></div>
            <div class='btnBox fixed'>
                <button type="button" class="btn orangeBtn" @click="save()">保存</button>
                <button type="button" class="btn redBtn" @click="goNext()">下一步</button>
            </div>
        </div>
    </div>
</template>
<script>
import vNavbar from "@/components/NavBar";
export default {
  components: { vNavbar },
  data() {
    return {
      selected: "2",//tab页数
      channelMod: "",
      params: [
        { id: "1", name: "基础信息" },
        { id: "2", name: "担保信息" },
        { id: "3", name: "资产信息" },
        { id: "4", name: "融资信息" }
      ],//tab数据
      channelList: [{ id: "1", name: "手机" }, { id: "2", name: "担保信息" }],
    };
  },
  computed: {},
  methods: {
    //添加个人担保
    addGuarantee(){
        this.$router.push("/AddGuarantee");
    },
    //添加法人担保
    addEGuarantee(){
        this.$router.push("/AddEGuarantee");
    },
    //保存
    save() {

    },
    //下一步
    goNext() {
      this.$router.push("/AssetInfo");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"

$rem=100
.BasicInfo {
  margin: (20/$rem)rem 0 0 0;
}
.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (31/$rem)rem (18/$rem)rem (31/$rem)rem;
        justify-content: space-between;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (16/$rem)rem 0 (10/$rem)rem;
        }
        .red{
            font-family: PingFangSC-Semibold;
            font-size: (26/$rem)rem;
            color: #D70B30;
        }
    }
}
.arrow {
    bg-image("../../imgs/rightarrow_icon");
    background-repeat: no-repeat;
    background-size: (14/$rem)rem (26/$rem)rem;
    background-position: 94% 50%;
    padding:(30/$rem)rem 0 (30/$rem)rem 0;
    margin: 0 (30/$rem)rem
    .row{
        height:(80/$rem)rem
        
    }
        
}

.gap{
    height: (140/$rem)rem
}
</style>



