<template>
    <div class="container ContractPre">

        <div class="detail-l">
            <div class="l-title">合同号 SANY20180603432423</div>
            <ul>
                <li>
                    <span class="l-t">业务日期</span>
                    <span class="l-v">钱多多123</span>
                </li>
                <li>
                    <span class="l-t">借款人姓名</span>
                    <span class="l-v">2000.00</span>
                </li>
                <li>
                    <span class="l-t">证件号码</span>
                    <span class="l-v">24月</span>
                </li>
                <li>
                    <span class="l-t">还款账号</span>
                    <span class="l-v">100000.00</span>
                </li>
                <li>
                    <span class="l-t">金融产品</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">合同金额</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">首付款</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">还款频率</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">融资期限</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">融资金额</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">还款方式</span>
                    <span class="l-v">正常</span>
                </li>
                <li>
                    <span class="l-t">融资比例</span>
                    <span class="l-v">正常</span>
                </li>
            </ul>
            <button type="button" @click="showDetail()">详情</button>
        </div>
        <div class="fixedBox">
            <button type="button" class="fixedBtn" @click="startAuth()">开始认证</button>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  computed: {},
  methods: {
    showDetail() {
      this.$router.push("/ContractDetail");
    }
  }
};
</script>

<style lang="scss" scoped>
$rem: 100;
.blue {
  color: #5c92fb !important;
}
.container.ContractPre {
  padding: 0;
  margin: 30rem/$rem 30rem/$rem;
  background: #ffffff;
  border: 1px solid #ededed;
  .detail-l {
    background: #ffffff;
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
      font-family: PingFangSC-Semibold;
      font-size: 30rem/$rem;
      color: #232323;
      padding: 31rem/$rem 0 18rem/$rem 31rem/$rem;
    }
    button {
      position: absolute;
      right: 37rem/$rem;
      top: 31rem/$rem;
      border: 0;
      font-size: 26rem/$rem;
      color: #de0313;
      background: #ffffff;
    }
  }
  .detail-l ul {
    margin: 0;
    padding: 20rem/$rem 30rem/$rem;
    list-style: none;
    font-size: 26rem/$rem;
    li {
      display: flex;
      padding: 6rem/$rem 0;
      .l-t {
        flex: 1;
        font-size: 26rem/$rem;
        color: #999999;
        text-align: left;
      }
      .l-v {
        flex: 1.8;
        font-size: 26rem/$rem;
        color: #333333;
        text-align: left;
      }
    }
  }
  .fixedBox {
    position: fixed;
    left: 0;
    bottom: 0;
    background: #ffffff;
    border: 1px solid #ededed;
    width: 100%;
    height: 120rem/$rem;
    padding: 0 0 60rem/$rem;
    text-align: center;
    .fixedBtn {
      width: 90%;
      //   margin: 20rem/$rem auto;
      background-image: linear-gradient(-133deg, #ff4d4d 0%, #dd0011 100%);
      border: 0;
      font-size: 30rem/$rem;
      padding: 19rem/$rem 0;
      color: #ffffff;
    }
  }
}
</style>



