<template>
    <div>
        <div class="container">
            <div class="detail-l">
                <ul class='bg-white'>
                    <li class='row borderB'>
                        <span class="col-5">证件类型</span>
                        <span class="col-5 text-r cifImg">身份证</span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">手机号码</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">姓名</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">证件号码</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">婚姻状况</span>
                        <span class="col-5 text-r">
                            <select class="selectBtn" v-model="channelMod" :class="{'selectBtn-d':channelMod==''}">
                                <option value="" selected>请选择</option>
                                <option v-for="(item,index) in channelList" :key="index">{{item.name}}</option>
                            </select>
                        </span>
                    </li>
                </ul>
            </div>
            <div class="detail-l">
                <div class="l-title">
                    <i>配偶信息</i>
                </div>
                <ul class='bg-white'>
                    <li class='row borderB'>
                        <span class="col-5">证件类型</span>
                        <span class="col-5 text-r cifImg">身份证</span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">配偶姓名</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">证件号码</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                </ul>
            </div>

            <div class="gap"></div>
            <div class='btnBox width80'>
                <button type="button" class="btn redBtn" @click="add()">确定</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      selected: "2",//tab页数
      channelMod: "",
      channelList: [{ id: "1", name: "手机" }, { id: "2", name: "担保信息" }]
    };
  },
  computed: {},
  methods: {
    //确定
    add() {
      this.$router.push("/GuaranteeInfo");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

$rem=100
.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (30/$rem)rem;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (10/$rem)rem;
        }
    }
    }
.gap{
    height: (140/$rem)rem
}
</style>



