<template>
    <div class="handel">
        <span class="date">{{date}}</span>
        <!-- <button class="btn" :class="stateClass">{{stateDesc}}</button> -->
        <mt-button v-bind:disabled="dis" type="danger" plain size="large" class="btn" :class="stateClass" @click.native="handleClick">{{stateDesc}}</mt-button>
    </div>   
</template>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl"
.handel
    display :flex
    flex-direction :row
    justify-content :space-between
    align-items :center
    .date
        color :#999999
    .btn
        width :1.8rem
        height :0.6rem
        &.finish
            border:none
            color :#4A90E2
            opacity: 1
        &.process
            border:none
            color :orange
</style>


<script>

export default {
    props: {
      date: {
        type: String,
        default:'2018/12/12'
      },
      state: {
        type: Number,
        default:0
      },
      productid: {
        type: String,
        default:''  
      }
    },
    data() {
        return {
            dis:true,
            lists:[]
        }
    },
    computed:{
        stateDesc () {
            if (this.state === 0) {
                this.dis = false;
                return '点击办理';
            } else if (this.state === 1) {
                this.dis = true;
                return '已办理';
            } else {
                return '办理中';
            }
        },
        stateClass (){
            if (this.state === 1) {
                return 'finish';
            } else if (this.state === 0) {
                return;
            }else{
                return 'process';
            }
        }
    },
    mounted(){

    },
    methods: {
        handleClick: function() {
            console.log(this.productid)
        }
    }
};
</script>



