<template>
  <mt-header fixed  :title="$store.state.header.title" v-show="$store.state.header.showHeader" :style="{'height':headerHeight,'padding-top':marginTop}">
    <a @click="doIt" slot="left">
    <mt-button icon="back">返回</mt-button>
    </a>
    <mt-button icon="more" slot="right"></mt-button>
</mt-header>
</template>
<script>
import {goBack} from '@/common/common.js'
export default {
  data(){
      return{
          marginTop:0,
          headerHeight:0
      }
  },
  mounted(){
        this.doIt()
    //  this.marginTop=context.getParam("HeadHeight");
        
  },
  methods:{
      //点击返回
      doIt(){
          this.headerHeight=parseFloat(context.sessionGetString("HeadHeight"))+44+"px";
          this.marginTop=context.sessionGetString("HeadHeight")+"px";
            alert(this.marginTop+"test页面:"+this.headerHeight)
        //   this.goBack();
      },
      getHeight(){
            this.headerHeight=context.sessionGetString("HeadHeight");
            alert("test页面:"+this.headerHeight)
          
        }
  }
}
</script>
<style>
.mint-header.is-fixed{
    z-index: 20;
}
</style>





