<template>
   <div class="contract">
       <!-- 为了好看我加的 -->
       123467890
        <div v-for="(item,index) in faceSignData" :key="index" :class="[index == 0 ? 'boxs' : 'box']">
            <div class="titles">
                <span>合同号</span><span class="contract_num">{{item.contractNum}}</span>
            </div>
            <div class="contractContent">
                <div class="content_list">
                    <div class="list_left">
                        借款人
                    </div>
                    <div class="list_right">
                        {{item.name}}
                    </div>  
                </div>
                <div class="content_list">
                    <div class="list_left">
                        合同金额
                    </div>
                    <div class="list_right">
                        {{item.money}}
                    </div>  
                </div>
                <div class="content_list">
                    <div class="list_left">
                        贷款限制
                    </div>
                    <div class="list_right">
                        {{item.time}}
                    </div>  
                </div>
                <div class="content_list">
                    <div class="list_left">
                        剩余本金
                    </div>
                    <div class="list_right">
                        {{item.surplus}}
                    </div>  
                </div>
                <div class="content_list">
                    <div class="list_left">
                        合同状态
                    </div>
                    <div class="list_right list_blue">
                        {{item.state}}
                    </div>  
                </div>
                <button class="contract_btn" @click="clickJump">
                    结清
                </button>
            </div>
         </div>     
      </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name: 'settlement',
  data () {
    return {
        faceSignData:[
                {name:"钱多多",money:"1,300,000",time:"36月",contractNum:"SANY20187633748",surplus:"563,898",state:"正常"},
                {name:"钱很多",money:"1,300,000",time:"36月",contractNum:"SANY20187633748",surplus:"563,898",state:"正常"},
                {name:"钱多多",money:"1,300,000",time:"36月",contractNum:"SANY20187633748",surplus:"563,898",state:"正常"},
                {name:"钱太多",money:"1,300,000",time:"36月",contractNum:"SANY20187633748",surplus:"563,898",state:"正常"}
            ]
    }
  },
  methods:{
       clickJump(){
          this.$router.push({path: '/face/faceId'})
      },
      next(){
        //   this.$router.push({path: '/registerpwd'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style scoped>
    .contract{
        background: #dddddd;
    }
    .mint-header{
       background: linear-gradient(#FF4D4D, #DD0011);
       min-height: 0.7rem;   
    }
    .boxs{
        width: 7.5rem;
        height: 3.77rem;
        background: #ffffff;
        font-family: "PingFangSC-Regular";
    }
    .box{
        width: 7.5rem;
        height: 3.77rem;
        background: #ffffff;
        font-family: "PingFangSC-Regular";
        margin-top: 0.2rem; 
    }
    .titles{
        width: 7.19rem;
        height: 0.91rem;
        margin-left: 0.31rem;
        border-bottom-color: #DDDDDD;
        border-bottom-style: solid;
        border-bottom-width: 1px;
        font-size: 0.3rem;
        color: #232323;
        line-height: 0.42rem;
        padding-top: 0.24rem;
        box-sizing: border-box;
        font-weight: bold;
    }
    .contract_num{
        margin-left: 0.31rem; 
    }
    .contractContent{
        width: 7.5rem;
        height: 2.84rem;
        border-bottom-color: #ededed;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        position: relative;
    }
    .content_list{
        display: flex;
        font-size: 0.26rem;
        line-height: 0.37rem;
    }
    .list_left{
        width: 2.71rem;
        margin-top: 0.16rem;
        color: #999999;
        margin-left: 0.31rem; 
    }
    .list_right{
        color: #333333;
        margin-top: 0.16rem;
    }
    .contract_btn{
        position: absolute;
        bottom: 0.31rem;
        right: 0.31rem;
        width: 1.6rem;
        height: 0.6rem;
        background-color: #ffffff;
        border: 1px solid #DE0313;
        outline:none;
        color: #DE0313;
        font-size: 0.26rem;
    }
    .list_blue{
        color: #5C92FB;
    }
</style>




