<template>
   <div>
        <div class="appoint">
            <div class="notice">
              <p>免费获取最低报价</p>
              <p>请填写一下信息告诉我们（信息保密，不对外公开)</p>
              <p>官方服务将在1小时内联系您，请保持电话通畅</p>
            </div>
            <h1 class="divide"></h1>
            <div class="appoint_msg">
                <div class="lt">
                       <div class="left">
                         <span>姓名</span> 
                        </div>
                       <div class="right">
                          <input type="text" v-model="userName" placeholder="请输入" class="input">
                       </div>
                </div>
                <div class="lt">
                       <div class="left">
                         <span>证件号码</span> 
                        </div>
                       <div class="right">
                          <input type="text" v-model="idNumber" placeholder="请输入" class="input">
                       </div>
                </div>
                <div class="lt">
                        <div class="left">
                         <span>地区</span> 
                        </div>
                       <div class="right">
                          <input type="text" v-model="area" class="input area" @click="selArea" readonly/>
                          <mt-popup v-model="popupVisible"  position="middle">
                              <mt-picker :slots="myAddressSlots" @change="onMyAddressChange"></mt-picker>
                         </mt-popup>
                       </div>
                </div>
            </div>
            <div class="Additional">
                <span>完善以下需求便于我们更针对的提供服务（可选)</span>
            </div>
             <div class="appoint_msg">
               <div class="lt">
                    <div class="left">
                         <span>公司</span> 
                        </div>
                       <div class="right">
                          <input type="text" v-model="company" placeholder="个人客户无需填写" class="input">
                       </div>
               </div>
               <div class="lt">
                    <div class="left">
                         <span>周期</span> 
                        </div>
                       <div class="right">
                          <select v-model="date" class="input">
                               <option value="">请选择</option>
                               <option value="">一个月内</option>
                               <option value="">三个月内</option>
                               <option value="">半年内</option>
                          </select>
                       </div>
               </div>
               <div class="lt">
                       <div class="left">
                         <span>其他要求</span> 
                        </div>
                       <div class="right">
                          <textarea v-model="introduction" placeholder="请输入100字以内" class="input"></textarea>
                       </div>
               </div>

            </div>
            <mt-button type="danger" size="large" class="now_appoint" @click="now_apponit">立即预约</mt-button>
          </div>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
import myaddress from '../../common/pca.json'
export default {
  name: 'appoint',
  data () {
    return {
      userName:'',
      idNumber:'',
      company:'',
      date:'',
      introduction:'',
      popupVisible:false,
      area:'请选择',
      myAddressSlots: [
          {
            flex: 1,
            defaultIndex: 1,    
            values: Object.keys(myaddress),  //省份数组
            className: 'slot1',
            textAlign: 'center'
          }, 
          {
            divider: true,
            content: '-',
            className: 'slot2'
          },
           {
            flex: 1,
            values: [],
            className: 'slot3',
            textAlign: 'center'
          },
          {
            divider: true,
            content: '-',
            className: 'slot4'
          },
          {
            flex: 1,
            values: [],
            className: 'slot5',
            textAlign: 'center'
          }
        ],
        myAddressProvince:'省',
        myAddressCity:'市',
        myAddresscounty:'区/县',
    }
  },
  methods:{
    //picker 回调事件
    onMyAddressChange(picker, values){
       if(myaddress[values[0]]){
          picker.setSlotValues(1,Object.keys(myaddress[values[0]])); 
          picker.setSlotValues(2,myaddress[values[0]][values[1]]);
          this.area =  values[0] + '-' + values[1] + '-' + values[2]
        }
      },
      //选择地区
      selArea(){
        this.popupVisible = true;
      },
      //立即预约
      now_apponit(){
          Toast({
            message: `已预约，请等待审核。。。`,
            position: 'middle',
            duration: 1000
        });
      }
  },
  mounted(){
      this.$nextTick(() => {
        // this.myAddressSlots[0].defaultIndex = 0   
        this.area = '请选择'
      });
    }
}
</script>
<style >
     body{
       background: #fff;
     }
    .appoint{
      margin-top: 50px;
    }
   .appoint .notice{
     width: 90%;
     margin: auto;
   }
   .appoint_msg{
      margin-left: 5%;
   }
   .notice p{
        margin: 3% auto;
        font-size: 0.3rem;
     }
   .divide{
     height: 0.4rem;
     background: #F4F4F4;
   }
  .Additional{
       background: #F4F4F4;
       height: 0.8rem;
       line-height: 0.8rem;
     }
    .Additional span{
         margin-left: 5%;
         color: #999;
         font-size: 0.3rem;
    } 
    .lt{
       display: flex;
       height: 1rem;
       line-height: 1rem;
       border-bottom: 1px solid #DDDDDD;
    }
    .left{
      width: 20%;
    }
    .right{
      width: 80%;
      text-align: right;
    }
    .input{
       text-align: right;
       color: #999999;
       font-size: 0.3rem;
       margin-top: 5px;
       margin-right: 10px;
       outline: none;
       border: none;
       background: none;
       appearance: none;
       -moz-appearance: none;
       -moz-appearance: none;
    }
    .area{
      width: 5.5rem;
    }
    .mint-popup{
      width: 100%;
    }
    .arrow{
      height: 0.3rem;
      position: relative;
      right:5px;
      top:2px;
    }
  .now_appoint{
      background: linear-gradient(#FF4D4D, #DD0011);
      width: 80%;
      margin: 5% auto;
   } 
.mint-cell-text{
      color: #333;
   }
   .mint-field-core{
     color: #999;
   }
</style>




