<template>
    <div class="wrapper">
        <div class="company_icon">
            <div class="company boder-1px">
                <div class="imgurl"></div>
            </div>
            <h1 class="title">公司介绍</h1>
        </div>
        <div class="content">
            <p class="text">{{section0}}</p>
            <p class="text">{{section1}}</p>
            <p class="text">{{section2}}</p>
        </div>
    </div>
</template>

<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/mixin.styl"
.wrapper
    position :absolute
    width :100%
    height :100%
    background-color :#ffffff
    .company_icon
        margin-top :0.36rem
        .company
            display:flex
            justify-content :center
            align-items :center
            .imgurl
                width :0.93rem
                height :0.93rem
                bg-image('company_icon')
                background-size :0.93rem 0.93rem
                background-repeat :no-repeat
        .title
            margin-top :0.18rem
            font-size: 0.36rem
            line-height :0.5rem
            font-family :'PingFangSC-Semibold'
            font-weight :700
            color :#333333 
            text-align: center
    .content
        margin-top :0.61rem
        padding: 0 0.3rem 0 0.3rem
        background-color: #ffffff
        font-size: 0
        & div:last-child
            margin: 0
        .text
            font-family: 'PingFangSC-Regular'
            font-size: 0.28rem
            line-height: 0.42rem
            color: #333333
            vertical-align: top
            margin-bottom: 0.2rem
            text-align: justify
            text-indent:2em

</style>


<script>
export default {
  name: "About",
  data() {
    return {
        section0:"三一汽车金融有限公司于2010年10月获中国银监会批准开业，是中国工程机械行业首家汽车金融公司，也是湖南省首家汽车金融公司，注册资本8亿元人民币。现有员工91人，其中硕士以上学历占36%，75%以上员工具有金融行业从业经验或汽车生产销售管理经验。公司主要面向工程机械行业提供金融服务，业务品种涵盖贷款、租赁、保险、信托等众多领域，能为客户提供一站式、全流程的整体解决方案，并在全球各个市场形成各具特色的金融产品、服务和业务模式。",
        section1:"三一汽车金融有限公司拥有完善的风险管控和转移体系，以及强大的金融服务和金融资产运营能力，并随着业务发展延伸至金融的各个领域。三一汽车金融依托三一集团主业发展，将成为集团主业发展和全球扩张的助推器。",
        section2:"三一汽车金融有限公司将从国内起步，逐步扩展到美国、德国、新加波、印度、巴西、南非、沙特等地，并进一步向纵深发展，建立全球范围内统一管理、相互协调的支持企业发展金融业务板块空间布局。"
    };
  },
  methods: {
  }
};
</script>



