<template>
   <div class="contract">
        <div v-for="(item,index) in billData" :key="index" :class="[index == 0 ? 'list_ones' : 'list_one']">
            <div class="titles">
                <span class="title_left">{{item.left}}</span>
                 <i class="arrow" @click="next(item.path)"></i>
                <div class="title_right" @click="next(item.path)">{{item.right}}</div>
            </div>
            <div class="title_type">
                {{item.type}}
            </div>
            <div class="title_money">   
                {{item.money}}
            </div>
            <div class="still" v-if="item.time">
                {{item.time}}
            </div>
        </div>
    </div>    
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name: 'billSummary',
  data () {
    return {
       billData:[
           {left:"本月总账单",right:"账单明细",type:"应用金额",money:"¥1,300,000",time:"还款日 04/13",path:"/bill/Detailed"},
           {left:"历史账单",right:"历史账单明细",type:"累计扣款金额",money:"¥300,000",time:"",path:"/bill/History"},
           {left:"逾期信息",right:"逾期明细",type:"逾期金额",money:"¥300,000",time:"",path:"/bill/overdue"},
       ]
    }
  },
  methods:{
      next(x){
          this.$router.push({path: x})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/mixin.styl"
    .contract{
        /* background: #dddddd; */
        font-family: 'PingFangSC-Regular';
    }
    .mint-header{
       background: linear-gradient(#FF4D4D, #DD0011);
       min-height: 0.7rem;   
    }
    .list_one{
        background: #ffffff;
        padding-top: 0.22rem;
        padding-bottom: 0.36rem;
        margin-top: 0.2rem;
    }
    .list_ones{
        background: #ffffff;
        padding-top: 0.22rem;
        padding-bottom: 0.36rem;
    }
    .titles{
        width: 7.5rem;
        height: 0.42rem;
        /* display: flex; */
        /* align-items: center; */
        overflow: hidden;
    }
    .title_left{
        font-size: 0.3rem;
        color: #232323;
        float: left;
        line-height: 0.42rem;
        margin-left: 0.3rem;
    }
    .arrow{
        width: 0.12rem;
        height: 0.22rem;
        bg-image('rightarrow_icon')
        background-size :100% 100%;
        background-repeat :no-repeat;
        margin-right: 0.32rem;
        display: block;
        float: right;
        margin-top: 0.11rem;
    }
    .title_right{
        font-size: 0.26rem;
        color: #999999;
        float: right;
        line-height: 0.42rem;
        margin-right: 0.19rem;
    }
    .title_type{
        font-size: 0.26rem;
        color: #999999;
        line-height: 0.37rem;
        text-align: center;
        margin-top: 0.16rem;
    }
    .title_money{
        font-size: 0.48rem;
        color: #333333;
        text-align: center;
        line-height: 0.67rem;
    }
    .still{
        font-size: 0.26rem;
        color: #333333;
        text-align: center;
        margin-top: 0.23rem;
    }
</style>




