<template>
   <div class="contract">
       <i class="finger"></i>
       <div class="finger_text">指纹登录</div>
       <div class="switch">
           <div class="switch_text">指纹登录</div>
           <mt-switch v-model="value"></mt-switch>
       </div>
   </div>
</template>
<script>
import { Toast, Switch } from 'mint-ui';
export default {
  name: 'fingerprint',
  data () {
    return {
       value:true
    }
  },
  methods:{
      next(){
        //   this.$router.push({path: '/registerpwd'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/mixin.styl"
    .contract{
        width: 7.5rem;
        padding-top: 0.94rem;
    }
    .finger{
        bg-image('fingerprint_icon');
        background-size :100% 100%;
        background-repeat :no-repeat;
        display: block;
        width: 1.28rem;
        height: 1.28rem;
        margin: 0 auto 0;
    }
    .finger_text{
        font-family: "PingFangSC-Semibold";
        font-size: 0.3rem;
        color: #333333;
        line-height: 0.42rem;
        text-align: center;
        margin-top: 0.25rem;
    }
    .switch{
        width: 7.5rem;
        height: 1rem;
        margin-top: 0.99rem;
        border: 1px solid #EDEDED;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .switch_text{
        font-family: 'PingFangSC-Regular';
        font-size: 0.3rem;
        color: #333333;
        line-height: 0.42rem;
        margin-left: 0.29rem;
    }
    .mint-switch{
        margin-right: 0.33rem;
    }
    .mint-switch-input:checked + .mint-switch-core{
        background-color: red!important;
    }
</style>




