<template>
    <div class="container ContractPre">

        <div class="detail-l">
            <div class="l-title">合同号 SANY20180603432423</div>
            <ul>
                <li>
                    <span class="l-t">合同金额</span>
                    <span class="l-v">1300.00</span>
                </li>
                <li>
                    <span class="l-t">融资金额</span>
                    <span class="l-v">2000.00</span>
                </li>
                <li>
                    <span class="l-t">合同利率</span>
                    <span class="l-v">3%</span>
                </li>
                <li>
                    <span class="l-t">合同期限</span>
                    <span class="l-v">2018/5/8-2019/5/8</span>
                </li>
                <li>
                    <span class="l-t">还款方式</span>
                    <span class="l-v">等额本息</span>
                </li>
                
            </ul>
        </div>

        <div class="detail-l">
            <div class="l-title">还款计划明细</div>
            <ul>
                <li class="l-table-t">
                    <span class="">还款日期</span>
                    <span class="">还款金额</span>
                    <span class="">贷款余额</span>
                </li>
                <li class="l-table" v-for="(item,index) in getList" :key="index">
                    <span class="">合同金额</span>
                    <span class="">1312312300.00</span>
                    <span class="">1300.00</span>
                </li>
                <li class="table-more borderB" @click="showMore" v-if="getListLen">
                    <a>更多展开</a>
                </li>
                <li class="table-total">
                    <span>总计     13000.00</span>
                </li>
            </ul>
        </div>

    </div>
</template>
<script>
export default {
  data() {
    return {
        len:1,//当前页
        size:5,//一页显示条数
        List:[1,2,3,4,5,6,7,8]
    };
  },
  computed: {
      //获取当前显示数据
      getList(){
          if(this.List.length<=5){
              return this.List
          }else{
              return this.List.slice(0,this.len*this.size)
          }
      },
      //是否展示显示更多
      getListLen(){
          if(this.List.length<=this.len*this.size){
              return false
          }else{
              return true
          }
      }
  },
  methods: {
    //更多展开
    showMore(){
        this.len++;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"

$rem=100
.blue {
  color: #5c92fb !important;
}
.container.ContractPre {
  padding: 0;
  margin: (30/$rem)rem (30/$rem)rem;
//   background: #ffffff;
  border: 1px solid #ededed;
  .detail-l {
    background: #ffffff;
    border-bottom: 1px solid #ededed;
    position: relative;
    margin: (20/$rem)rem 0;
    .l-title {
      font-family: PingFangSC-Semibold;
      font-size: (30/$rem)rem;
      color: #232323;
      padding: (30/$rem)rem 0 (18/$rem)rem (30/$rem)rem;
    }
    button {
      position: absolute;
      right: (37/$rem)rem;
      top: (30/$rem)rem;
      border: 0;
      font-size: (26/$rem)rem;
      color: #de0313;
      background: #ffffff;
    }
  }
  .detail-l ul {
    margin: 0;
    padding: (20/$rem)rem (30/$rem)rem;
    list-style: none;
    font-size: (26/$rem)rem;
    li {
      display: flex;
      padding: (6/$rem)rem 0;
      .l-t {
        flex: 1;
        font-size: (26/$rem)rem;
        color: #999999;
        text-align: left;
      }
      .l-v {
        flex: 1.8;
        font-size: (26/$rem)rem;
        color: #333333;
        text-align: left;
      }
    }
    li.l-table-t{
        color: #999999;
        font-size: (26/$rem)rem;
        justify-content: space-around;
        span{
            flex:1;
            text-align: center;
        }
    }
    li.l-table{
        color: #333333;
        font-size: (26/$rem)rem;
        justify-content: space-around;
        span{
            flex:1;
            text-align: center;
        }
    }
    li.table-more{
        justify-content: center;
        padding: (38/$rem)rem 0;
        a{
            border: 1px solid #6E9EFC;
            color:#6E9EFC;
            padding: (10/$rem)rem (26/$rem)rem
        }
    }
    li.table-total{
        font-family: PingFangSC-Semibold;
        font-size: (30/$rem)rem;
        color: #333333;
        padding: (30/$rem)rem 0 (20/$rem)rem;
        text-align: center;
        justify-content: center;
    }
  }
  .fixedBox {
    position: fixed;
    left: 0;
    bottom: 0;
    background: #ffffff;
    border: 1px solid #ededed;
    width: 100%;
    height: (120/$rem)rem;
    padding: 0 0 (60/$rem)rem;
    text-align: center;
    .fixedBtn {
      width: 90%;
      //   margin: 20rem/$rem auto;
      background-image: linear-gradient(-133deg, #ff4d4d 0%, #dd0011 100%);
      border: 0;
      font-size: (30/$rem)rem;
      padding: (20/$rem)rem 0;
      color: #ffffff;
    }
  }
}
</style>



