<template>
    <div class="aghandel">
        <mt-button class="btn" type="danger" plain size="large" @click.native="handleClick">{{stateDesc}}</mt-button>
    </div> 
</template>

<style lang="stylus" rel="stylesheet/stylus">
.aghandel
    position :absolute
    right :0.31rem
    bottom :0.31rem
    .btn
        width :1.8rem
        height :0.6rem
</style>


<script>
import { Button } from 'mint-ui';
export default {
    props: {
      type: {
        type: String,
        default:'2018/12/12'
      },
      state: {
        type: Number,
        default:-1
      },
      productid: {
        type: String,
        default:''
      }
    },
    data() {
        return {}
    },
    computed:{
        stateDesc () {
            if (this.state === 0 && this.type === "询价") {
                return '预约处理';
            } else if (this.state === 0 && this.type === "面签") {
                return '面签处理';
            } else if(this.state === 1) {
                return '查看';
            }
        }
    },
    mounted(){

    },
    methods: {
        handleClick:function() {
            console.log(this.productid);
        }
    }
};
</script>



