<template>
    <div>
        <div class="container">
            <div class="detail-l">
                <div class="l-title">
                    <i>基础信息</i>
                </div>
                <ul class='bg-white'>
                    
                    <li class='row borderB'>
                        <span class="col-5">类型</span>
                        <span class="col-5 text-r">
                            <select class="selectBtn" v-model="channelMod" :class="{'selectBtn-d':channelMod==''}">
                                <option value="" selected>请选择</option>
                                <option v-for="(item,index) in channelList" :key="index">{{item.name}}</option>
                            </select>
                        </span>
                    </li>
                    
                    <li class='row borderB'>
                        <span class="col-5">姓名</span>
                        <span class="col-5 text-r cifImg">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">身份证</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">手机号</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                </ul>
            </div>
            <div class="detail-l">
                <div class="l-title">
                    <i>征信授权</i>
                </div>
                <ul class='bg-white agreementBox'>
                    
                    <li class='text-c'>
                        <span class="col-10 ">由于业务需要，需您配合进行征信查询授权，以便于更好的为您提供服务。</span>
                    </li>
                    <li class='row agreeCenter'>
                            <label for="agreement" class="agreement">
                            <input type='radio' id="agreement" name="agreement" v-model="agreement">
                            您已同意授权三一金融进行征信信息查询
                            </label>
                      
                    </li>
                    <div class='btnBox repayBtn'>
                        <button type="button" class="btn" @click="gotoPlan()">授权书签名</button>
                    </div>
                </ul>
            </div>


            <div class="gap"></div>
            <div class='btnBox fixed'>
                <button type="button" class="btn orangeBtn" @click="goApply()">继续申请</button>
                <button type="button" class="btn redBtn" @click="goApply()">确定</button>
            </div>
        </div>
    </div>
</template>
<script>
import vNavbar from "@/components/NavBar";
export default {
  data() {
    return {
      channelMod: "",
      agreement:"",
      channelList: [{ id: "1", name: "手机" }, { id: "2", name: "担保信息" }],
    };
  },
  computed: {},
  methods: {

    //提交
    goApply() {
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"

$rem=100
.BasicInfo {
  margin: 20px 0 0 0;
}
.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (30/$rem)rem;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (10/$rem)rem;
        }
    }
    }
.gap{
    height: (140/$rem)rem
}
.repayBtn{
    .btn{
        font-size: (30/$rem)rem 0;
        color: #E00616;
        border: 1px solid #DF0414;
        background: #ffffff;
        box-shadow: 0 4px 15px 0 rgba(219,31,30,0.30);
        padding: (20/$rem)rem (75/$rem)rem;
    }
}
.uploadBox{
    display: flex;
    flex-wrap: wrap;
    .uploadIcon{
        width: (180/$rem)rem;
        height: (180/$rem)rem;
        margin: (30/$rem)rem (30/$rem)rem;
        img{
            width:100%;
            border:1px solid #eee;
        }
    }
    
}
.agreementBox{
    padding: (34/$rem)rem (70/$rem)rem;
    .agreeCenter{
        justify-content:center;
    }
    .agreement{
        font-size: (24/$rem)rem;
        color: #DF0414;
    }
}
</style>



