<template>
    <div>
        <v-navbar :selected='selected' :params='params'></v-navbar>
        <div class="container">
            <div class="detail-l">
                <div class="l-title">
                    <i>基础信息</i>
                </div>
                <ul class='bg-white'>
                    <li class='row borderB'>
                        <span class="col-5">资产总价</span>
                        <span class="col-5 text-r">1000.00</span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">金融产品</span>
                        <span class="col-5 text-r">
                            <select class="selectBtn" v-model="channelMod" :class="{'selectBtn-d':channelMod==''}">
                                <option value="" selected>请选择</option>
                                <option v-for="(item,index) in channelList" :key="index">{{item.name}}</option>
                            </select>
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">融资期限(月)</span>
                        <span class="col-5 text-r">36</span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">融资金额</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">首付款比例(%)</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">首付款</span>
                        <span class="col-5 text-r">0</span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">还款方式</span>
                        <span class="col-5 text-r">
                            <select class="selectBtn" v-model="channelMod" :class="{'selectBtn-d':channelMod==''}">
                                <option value="" selected>请选择</option>
                                <option v-for="(item,index) in channelList" :key="index">{{item.name}}</option>
                            </select>
                        </span>
                    </li>
                    <!-- <li class='row borderB'>
                        <button type="button" class="btn">还款计划预览</button>
                    </li> -->
                    <div class='btnBox repayBtn'>
                        <button type="button" class="btn" @click="gotoPlan()">还款计划预览</button>
                    </div>
                </ul>
            </div>

            <div class="detail-l">
                <div class="l-title">
                    <i>上传附件</i></div>
                <ul class='bg-white uploadBox'>
                    <li class='uploadIcon'>
                        <img src=""/>
                    </li>
                    <li class='uploadIcon'>
                        <img :src='uploadImg'/>
                    </li>
                   <li class='uploadIcon'>
                        <img :src='uploadImg'/>
                    </li>
                    <li class='uploadIcon'>
                        <img :src='uploadImg'/>
                    </li>
                    <li class='uploadIcon'>
                        <img :src='uploadImg'/>
                    </li>
                </ul>
            </div>

            <div class="gap"></div>
            <div class='btnBox fixed'>
                <button type="button" class="btn redBtn" @click="goApply()">提交申请</button>
            </div>
        </div>
    </div>
</template>
<script>
import vNavbar from "@/components/NavBar";
export default {
  components: { vNavbar },
  data() {
    return {
      selected: "4",//tab页数
      channelMod: "",
      params: [
        { id: "1", name: "基础信息" },
        { id: "2", name: "担保信息" },
        { id: "3", name: "资产信息" },
        { id: "4", name: "融资信息" }
      ],//tab数据
      channelList: [{ id: "1", name: "手机" }, { id: "2", name: "担保信息" }],
      uploadImg:require('../../imgs/uploading_icon@2x.png')//上传图标
    };
  },
  computed: {},
  methods: {
    //还款计划预览
    gotoPlan() {
        this.$router.push("/RepayPlan");

    },
    //提交申请
    goApply() {
      alert("申请成功")
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"

$rem=100
.BasicInfo {
  margin: 20px 0 0 0;
}
.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (30/$rem)rem;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (10/$rem)rem;
        }
    }
    }
.gap{
    height: (140/$rem)rem
}
.repayBtn{
    padding: (36/$rem)rem 0;
    .btn{
        font-size: (30/$rem)rem 0;
        color: #E00616;
        border: 1px solid #DF0414;
        background: #ffffff;
        box-shadow: 0 4px 15px 0 rgba(219,31,30,0.30);
        padding: (20/$rem)rem (75/$rem)rem;
    }
}
.uploadBox{
    display: flex;
    flex-wrap: wrap;
    .uploadIcon{
        width: (180/$rem)rem;
        height: (180/$rem)rem;
        margin: (30/$rem)rem (30/$rem)rem;
        img{
            width:100%;
            border:1px solid #eee;
        }
    }
    
}

</style>



