<template>
  <div class="container">
    <div class="detail-l">
      <ul class="list">
        <h2 class="l-title">合同号 SANY20180603432423</h2>
        <li>
          <span class="l-t">合同金额</span>
          <span class="l-v">2000.00</span>
        </li>
        <li>
          <span class="l-t">融资金额</span>
          <span class="l-v">1,300,000</span>
        </li>
        <li>
          <span class="l-t">合同利率</span>
          <span class="l-v">3%</span>
        </li>
        <li>
          <span class="l-t">合同期限</span>
          <span class="l-v">2016/12/21-2019/12/20</span>
        </li>
        <li>
          <span class="l-t">剩余本金</span>
          <span class="l-v blue">798,347</span>
        </li>
        <li>
          <span class="l-t">还款账号</span>
          <span class="l-v blue">6324364758778765</span>
        </li>
      </ul>
      <div class="repaymentMethod">
        <span class="title">还款方式</span>
        <span class="text">提前全部还款</span>
      </div>
      <div class="repaymentDate">
        <span class="title">还款日期</span>
        <input class="text" placeholder="请填写合同变更日期"/>
      </div>
      <div class="principalAmount">
        <span class="title">还款本金金额</span>
        <span class="text">789,347</span>
      </div>
      <div class="bottom">
        <button class="btn" @click="showPre()">确认</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  computed: {},
  methods: {
    showPre() {
      this.$router.push("/prepaymentConfirm");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"
.container
  margin :0.31rem 0.32rem 0 0.32rem
  .detail-l
    font-family :'PingFangSC-Semibold'
    .list
      background-color: #ffffff
      padding :0.44rem 0.33rem 0.48rem 0.33rem
      .l-title
        margin-bottom: 0.27rem;
        font-size :0.3rem
        color :#232323
        line-height :0.42rem
        height :0.42rem
        font-weight :600
      li
        display :flex
        justify-content :center
        align-items :center
        .l-t,.l-v
          font-size :0.26rem
          line-height :0.37rem
          height :0.37rem
        .l-t
          flex :1
          color :#999999
        .l-v
          flex :2
          color :#333333
    .repaymentMethod
      display :flex
      margin-top:0.2rem
      justify-content :space-between
      align-items :center
      padding :0 0.32rem 0 0.32rem
      background-color :#ffffff
      height :0.9rem
      vertical-align :middle
      text-align :center
      .title,.text
        font-size :0.3rem
        height :0.42rem
        line-height :0.42rem
    .repaymentDate
      display :flex
      margin-top:0.2rem
      justify-content :space-between
      align-items :center
      padding :0 0.32rem 0 0.32rem
      background-color :#ffffff
      height :0.9rem
      vertical-align :middle
      text-align :center
      .title,.text
        font-size :0.3rem
        height :0.42rem
        line-height :0.42rem
      .text
        width :2.7rem
        color :#CCCCCC
    .principalAmount
      display :flex
      margin-top:0.2rem
      justify-content :space-between
      align-items :center
      padding :0 0.32rem 0 0.32rem
      background-color :#ffffff
      height :0.9rem
      vertical-align :middle
      text-align :center
      .title,.text
        font-size :0.3rem
        height :0.42rem
        line-height :0.42rem
    .bottom
      display :flex
      justify-content :center
      align-items :center
      position :fixed
      bottom :0
      left :0
      height :1.2rem
      width :7.5rem
      background-color: #ffffff;
      .btn
        flex :0 0 5.6rem
        height :0.8rem
        text-align :center
        font-size :0.3rem
        line-height :0.42rem
        color :#FFFFFF 
        bg-image('form_btn_red_icon')
        border:none
</style>



