<template>
    <div>
        <!-- <v-navbar :selected='selected' :params='params'></v-navbar> -->
        <div class="container">
            <div class="detail-l">
                <ul class='bg-white'>
                    <li class='row borderB'>
                        <span class="col-5">出资方</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">证件类型</span>
                        <span class="col-5 text-r">
                            <select class="selectBtn" v-model="channelMod" :class="{'selectBtn-d':channelMod==''}">
                                <option value="" selected>请选择</option>
                                <option v-for="(item,index) in channelList" :key="index">{{item.name}}</option>
                            </select>
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">证件号码</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">出资金额</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">持股比例</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    
                </ul>
            </div>

            <div class="gap"></div>
            <div class='btnBox width80'>
                <button type="button" class="btn redBtn" @click="add()">确定</button>
            </div>
        </div>
    </div>
</template>
<script>
import vNavbar from "@/components/NavBar";
export default {
  components: { vNavbar },
  data() {
    return {
      selected: "2",//tab页数
      channelMod: "",
      params: [
        { id: "1", name: "基础信息" },
        { id: "2", name: "担保信息" },
        { id: "3", name: "资产信息" },
        { id: "4", name: "融资信息" }
      ],//tab数据
      channelList: [{ id: "1", name: "手机" }, { id: "2", name: "担保信息" }],
    };
  },
  computed: {},
  methods: {
    //确定
    add() {
      this.$router.push("/EBasicInfo");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"

$rem=100

.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (30/$rem)rem;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (10/$rem)rem 0 (10/$rem)rem;
        }
    }
    }
.gap{
    height: (140/$rem)rem
}
</style>



