<template>
    <div class="financeProducts">
          <div class="title">
              <span>产品名称</span>
              <span class="rate">利率%</span>
              <span>还款周期</span>
          </div>
          <div class="productsBrief">
             <div v-for="(item,index) in items" :key="index" class="finance_product">
                      <span>{{item.productName}}</span>
                      <span>{{item.rate}}</span>
                      <span  class="cycle">{{item.repaymentCycle}}</span>
             </div>
         </div>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'financeProducts',
  data () {
    return {
        items:[
            {
                productName:'中宏售后回租72期',
                rate:'6.125',
                repaymentCycle:'36个月'
            },
             {
                productName:'中宏售后回租72期',
                rate:'6.125',
                repaymentCycle:'36个月'
            }
        ]
      }
  },
  methods:{
      
  }
}
</script>
<style lang="scss" scoped>
 .financeProducts{
     .title{
        font-family: PingFangSC-Semibold;
        font-size: 0.3rem;
        color: #333333;
        border-bottom: 1px solid #DDDDDD;
        display: flex;
        span{
            display: inline-block;
            flex-grow: 1;
            text-align: center;
            margin: 3% 0;
        }
        .rate{
            margin-left: 5%;
        }
     }
     .productsBrief{
         font-family: PingFangSC-Regular;
         font-size: 0.3rem;
         color: #333333;
        .finance_product{
            display: flex;
            border-bottom: 1px solid #DDDDDD;
            margin-left: 2%;
            span{
                display: inline-block;
                flex-grow: 1;
                height: 1rem;
                line-height: 1rem;
            }
            .cycle{
                margin-left: 5%;
            }
        }
     }
 }   
</style>




