<template>
    <div class="loantrial">
         <div class="banner">
             <img src="../../imgs/dkjs_banner@2x.png" alt="" class="img">
         </div>
         <div class="loan-info">
            <mt-cell title="金融产品"  to="/loan/financeproducts" is-link  value="请选择"></mt-cell>
            <mt-field label="融资金额" placeholder="请输入" type="text" v-model="financing_amount"></mt-field>
            <mt-field label="首付款比例" type="text" v-model="first_pay_rate"></mt-field>
            <mt-field label="首付款" placeholder="请输入" type="text" v-model="first_pay"></mt-field>
            <mt-field label="融资期限"  type="text" v-model="financing_limit"></mt-field>
            <mt-field label="还款频率"  type="text" v-model="repayment_frequency"></mt-field>
            <mt-field label="还款方式"  type="text" v-model="repayment_ways"></mt-field>
            <mt-field label="实际利息"  type="text" v-model="Interest"></mt-field>
            <mt-field label="预计放款时间"  type="date" v-model="loan_date"></mt-field>
         </div>
          <mt-button type="danger" size="large" class="trial" @click="trial">试算</mt-button>
    </div>
</template>
<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'loantrial',
  data () {
    return {
        financing_amount:'',
        first_pay_rate:'30%',
        first_pay:'',
        financing_limit:'36个月',
        repayment_frequency:'月还',
        repayment_ways:'部分月份只换金额不换本',
        Interest:'6.8%',
        loan_date:''
      }
  },
  methods:{
      trial(){
          this.$router.push({path:'/loan/repayscan'})
      }
  }
}
</script>
<style>
    .banner .img{
        width: 100%;
    }
   .loan-info  .mint-field-core{
        text-align: right;
    }
 .trial{
      background: linear-gradient(#FF4D4D, #DD0011);
      width: 80%;
      margin: 5% auto;
   }
</style>




