<template>
   <div class="contract">
        <div v-for="(item,index) in historyData" :key="index" :class="[index == 0 ? 'boxs' : 'box']">
            <div class="titles">
                <span>合同号</span><span class="contract_num">{{item.titlesNum}}</span>
            </div>
            <div class="contractContent">
                <div v-for="(item1,index1) in item.listsData" :key="index1" :class="[index1 == 0 ? 'content_lists' : 'content_list']">
                    <div class="list_left">
                        {{item1.type}}
                    </div>
                    <div class="list_right">
                        {{item1.num}}
                    </div>  
                </div>
                <button class="contract_btn" @click="clickJump()">
                    账单明细
                </button>
            </div>
         </div>     
      </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  name: 'Historys',
  data () {
    return {
        historyData:[
            {
                titlesNum:"SANY20187633748",
                listsData:[
                    {type:"借款人",num:"钱多多"},
                    {type:"合同金额",num:"1,300,000"},
                    {type:"融资金额",num:"1,300,000"},
                    {type:"已还本金",num:"1,300,000"},
                    {type:"已还利息",num:"1,300,000"},
                    {type:"已还费用",num:"1,300,000"},
                ]
            },
            {
                titlesNum:"SANY20187633748",
                listsData:[
                    {type:"借款人",num:"钱很多"},
                    {type:"合同金额",num:"1,300,000"},
                    {type:"融资金额",num:"1,300,000"},
                    {type:"已还本金",num:"1,300,000"},
                    {type:"已还利息",num:"1,300,000"},
                    {type:"已还费用",num:"1,300,000"},
                ]
            }
        ]
    }
  },
  methods:{
      //点击跳转合同信息，跳转过程中传值表示性ID
      clickJump(){
          this.$router.push({path: '/bill/repayment'})
      },
      get(){
        //  Toast({
        //     message: '获取验证码',
        //     position: 'middle',
        //     duration: 1000
        // });
      }
  }
}
</script>
<style scoped>
    .contract{
        background: #dddddd;
    }
    .mint-header{
       background: linear-gradient(#FF4D4D, #DD0011);
       min-height: 0.7rem;   
    }
    .boxs{
        width: 7.5rem;
        height: 4.3rem;
        background: #ffffff;
        font-family: PingFangSC-Regular;
    }
    .box{
        width: 7.5rem;
        height: 4.3rem;
        background: #ffffff;
        font-family: PingFangSC-Regular;
        margin-top: 0.2rem; 
    }
    .titles{
        width: 7.19rem;
        height: 0.91rem;
        margin-left: 0.31rem;
        border-bottom-color: #DDDDDD;
        border-bottom-style: solid;
        border-bottom-width: 1px;
        font-size: 0.3rem;
        color: #232323;
        line-height: 0.42rem;
        padding-top: 0.24rem;
        box-sizing: border-box;
        font-weight: bold;
    }
    .contract_num{
        margin-left: 0.31rem; 
    }
    .contractContent{
        width: 7.5rem;
        height: 3.37rem;
        border-bottom-color: #ededed;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        position: relative;
    }
    .content_list{
        display: flex;
        font-size: 0.26rem;
        line-height: 0.37rem;
        margin-top: 0.13rem;
    }
    .content_lists{
        display: flex;
        font-size: 0.26rem;
        line-height: 0.37rem;
        margin-top: 0.2rem;
    }
    .list_left{
        width: 2.71rem;
        color: #999999;
        margin-left: 0.31rem; 
    }
    .list_right{
        color: #333333;
    }
    .contract_btn{
        position: absolute;
        bottom: 0.51rem;
        right: 0.31rem;
        width: 1.6rem;
        height: 0.6rem;
        background-color: #ffffff;
        border: 1px solid #DE0313;
        outline:none;
        color: #DE0313;
        font-size: 0.26rem;
    }
</style>




