<template>
    <div>
        <div class="container">
            <div class="detail-l">
                <ul class='bg-white'>
                    <li class='row borderB'>
                        <span class="col-5">类别</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">子类别</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">资产单价（元）</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                    <li class='row borderB'>
                        <span class="col-5">数量</span>
                        <span class="col-5 text-r">
                            <input type="text" class="inputBtn" v-model="channelMod" placeholder="请输入">
                        </span>
                    </li>
                </ul>
            </div>
            <div class="gap"></div>
            <div class='btnBox width80'>
                <button type="button" class="btn redBtn" @click="add()">确定</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      selected: "2",//tab页数
      channelMod: "",
    };
  },
  computed: {},
  methods: {
    //点击确定
    add() {
      this.$router.push("/AssetInfo");
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

$rem=100
.detail-l {
    border-bottom: 1px solid #ededed;
    position: relative;
    .l-title {
        display: flex;
        align-items: center;
        font-size: (30/$rem)rem;
        color: #232323;
        padding: (18/$rem)rem (30/$rem)rem (18/$rem)rem (30/$rem)rem;
        i {
        font-style: normal;
        border-left: 6px solid #d70b30;
        padding: 0 (10/$rem)rem 0 (10/$rem)rem;
        }
    }
    }
.gap{
    height: (140/$rem)rem
}
</style>



